@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap');

.main-container {
  min-height: 100vh;
  height: auto;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-family: 'Fredoka One', cursive;
  color: #ff7a8a;
}

pre {
  font-size: 24px;
  color: #113a5d;
}

hr {
  margin-top: 20px;
  width: 60%;
}

.artist-input {
  width: 200px;
  height: 40px;
  border: 2px solid #113a5d;
  margin: 0px 10px;
  padding-left: 6px;
  font-size: 22px;
}

.song-input {
  width: 200px;
  height: 40px;
  border: 2px solid #113a5d;
  margin: 0px 10px;
  padding-left: 6px;
  font-size: 22px;
}

.search-button {
  width: 90px;
  height: 40px;
}

.icon {
  padding-right: 10px;
}

.error-message {
  color: red;
}
